import React, { useEffect, useState } from 'react';
import Button from '@material-ui/core/Button';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/core';
import { ACTIONS } from '../pages/Folder';

const useStyles = makeStyles((theme) => ({
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

const Notifier = ({ dispatch, ...props }) => {

  const classes = useStyles()

  const [openSnackbar, setOpenSnackbar] = useState(props.open) 
  const [snackbarMessage, setSnackbarMessage] = useState(props.message)
  const [snackbarUndoButton, setSnackbarUndoButton] = useState(props.undoButton)

  useEffect(() => {
    setOpenSnackbar(props.open)
    setSnackbarMessage(props.message)
    setSnackbarUndoButton(props.undoButton)
  }, [props.open, props.message, props.undoButton])

  return (
    <Snackbar
      className={classes.snackBar}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left',
      }}
      open={openSnackbar}
      autoHideDuration={5000}
      onClose={() => dispatch({ type: ACTIONS.CLOSE_SNACKBAR })}
      message={snackbarMessage}
      action={
        <>
          {snackbarUndoButton ? (
            <Button 
              color="inherit" 
              aria-label="undo-delete-link" 
              onClick={() => dispatch({ type: ACTIONS.UNDO_DELETE })}
            >
              Undo
            </Button>
          ) : ( "" )}
          <IconButton size="small" aria-label="close-snackbar" color="inherit" 
            onClick={() => dispatch({ type: ACTIONS.CLOSE_SNACKBAR })}
          >
            <CloseIcon fontSize="small" />
          </IconButton>
        </>
      }
    />
  )
}

export default Notifier