import React, { useReducer } from 'react';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import MenuItem from '@material-ui/core/MenuItem';
import SaveIcon from '@material-ui/icons/Save';
import { useState, useEffect } from "react"
import { Slide } from "@material-ui/core"

import firebase from "../firebase"
import axios from "axios"
import cheerio from "cheerio"

import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import { ACTIONS } from '../pages/Folder';
import { COLLECTION_ACTIONS } from '../collection';
import Notifier from './Snackbar';

const useStyles = makeStyles(theme => ({
  fabAdd: {
		position: "fixed",
		bottom: theme.spacing(9),
		right: theme.spacing(2),
		zIndex: 1000
  },
  fabCancel: {
    position: "fixed",
    bottom: theme.spacing(2),
    right: theme.spacing(2),
    zIndex: 1000
  },
  formInput: {
    width: "100%",
    margin: theme.spacing(1,0)
  },
  root: {
    padding: theme.spacing(8, 0, 18, 0)
  },
  icon: {
    marginRight: theme.spacing(1)
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
})

const formReducer = (state, action) => {
  switch (action.type) {
    case ACTIONS.CLOSE_SNACKBAR: {
      return {
        ...state,
        openSnackbar: false
      }
    }
    case ACTIONS.SHOW_SNACKBAR: {
      return {
        ...state,
        openSnackbar: true,
        snackbarMessage: action.payload.message
      }
    }
    default: return state
  }
}

const EditFormDialog = ({ dispatch, from,...props }) => {

  const classes = useStyles()
  const folders = useDetails()
  
  const initialState = {
    openSnackbar: false,
    snackbarUndoButton: false,
    snackbarMessage: "",
  }

  const handleClose = () => {
    resetFields()
    setOpen(false)
    dispatch({
      type: ACTIONS.CLOSE_EDIT_FORM
    })
  }

  const [linkInFolder, setLinkInFolder] = useState(props.folder_id)
  const [linkTitle, setLinkTitle] = useState(props.link_title)
  const [linkUrl, setLinkUrl] = useState(props.link_url)
  const [linkImg, setLinkImg] = useState(props.link_img)
  const [linkId, setLinkId] = useState(props.docId)
  const [collectionId, setCollectionId] = useState(props.collection_id)
  const [collectionName, setCollectionName] = useState(props.folder_name)
  const [collectionDesc, setCollectionDesc] = useState(props.folder_desc)
  const [open, setOpen] = useState(false)

  useEffect(() => {
    setLinkInFolder(props.folder_id)
    setLinkTitle(props.link_title)
    setLinkUrl(props.link_url)
    setLinkImg(props.link_img)
    setLinkId(props.docId)
    setOpen(props.edit)
    setCollectionId(props.collection_id)
    setCollectionName(props.folder_name)
    setCollectionDesc(props.folder_desc)
  }, [
    props.folder_id,
    props.link_title,
    props.link_url,
    props.link_img,
    props.edit,
    props.docId,
    props.collection_id,
    props.folder_name,
    props.folder_desc
  ])

  const [state, formDispatch] = useReducer(formReducer, initialState)

  const handleFormSubmit = e => {

    e.preventDefault()
    switch(props.mode) {
      case "collection": {
        firebase.firestore()
        .collection("collections")
        .doc(collectionId)
        .update({
          folder_name: collectionName,
          folder_desc: collectionDesc
        }).then(() => {
          dispatch({
            type: COLLECTION_ACTIONS.SAVE_UPDATED_FOLDER,
            payload: { message: "Collection successfully updated" }
          })
          console.log("Collection successfully updated")
        }).catch(err => {
          dispatch({
            type: COLLECTION_ACTIONS.SAVE_UPDATED_FOLDER,
            payload: { message: "Error updating collection" }
          })
          console.error("Error adding collection: ", err)
        })
        break
      }
      default: {
        if (from === "folder") {
          firebase.firestore()
          .collection("links")
          .doc(linkId)
          .update({
            link_title: linkTitle,
            link_url: linkUrl,
            folder_id: linkInFolder,
            link_img: linkImg,
            date_added: new Date(),
            is_archived: false
          }).then(() => {
            dispatch({
              type: ACTIONS.ADD_LINK,
              payload: { message: "Link successfully updated" }
            })
            console.log("Link successfully updated")
          }).catch(err => {
            dispatch({
              type: ACTIONS.ADD_LINK,
              payload: { message: "Error updating link" }
            })
            console.error("Error updating link on database: ", err)
          })
        } else {
          firebase.firestore()
          .collection("links")
          .add({
            link_title: linkTitle,
            link_url: linkUrl,
            folder_id: linkInFolder,
            link_img: linkImg,
            date_added: new Date(),
            is_archived: false
          }).then(() => {
            dispatch({
              type: ACTIONS.ADD_LINK,
              payload: { message: "Link added to your library" }
            })
            console.log("Link added to library")
          }).catch(err => {
            dispatch({
              type: ACTIONS.ADD_LINK,
              payload: { message: "Error adding link to library" }
            })
            console.error("Error adding link to library: ", err)
          })
        }
      }
    }
    resetFields()
    setOpen(false)
    
  }

  function resetFields() {
    setLinkInFolder(linkInFolder)
    setLinkTitle("")
    setLinkUrl("")
    setCollectionName("")
    setCollectionDesc("")
  }

  const handleGetDetails = (url) => {
    axios.get("https://cors-anywhere.herokuapp.com/" + url)
      .then(
        res => {
          console.log(res.data)
          const $ = cheerio.load(res.data)
          let title = $("meta[property='og:title']").attr("content")
          if(title === undefined) title = $('title')[0].children[0].data
          if(title === undefined) title = ""
          let img = $("meta[property='og:image']").attr("content")
          if(img === undefined) img = $("meta[property='twitter:image']").attr("content")
          if(img === undefined) {
            $("img").each((i, link) => {
              if(/^https:\/\//.test(link.attribs.src)) {
                img = link.attribs.src
              }
            })
          }
          if(img === undefined) img = ""
          
          setLinkImg(img)
          setLinkTitle(title)
          formDispatch({
            type: ACTIONS.CLOSE_SNACKBAR
          })
      }).catch(err => {
        console.log("THIS IS ERROR: ", err)
        formDispatch({
          type: ACTIONS.SHOW_SNACKBAR,
          payload: { message: "Snap! Failed to get link details" }
        })
      })
  }

  return (
    <div>
      <Dialog fullScreen open={open}
        TransitionComponent={Transition} 
        aria-labelledby="form-dialog-title"
      >
        <form onSubmit={handleFormSubmit} >
        <div className={classes.root}>
          <DialogTitle id="form-dialog-title">
            Edit {props.mode === "collection" ? props.mode : "link"}
          </DialogTitle>
          <DialogContent>
            {props.mode === "collection"
              ? <>
                <TextField required multiline
                  className={classes.formInput} size="small" variant="outlined"
                  id="collection-name" label="Collection name"
                  value={collectionName}
                  onChange={e => setCollectionName(e.target.value)}
                />
                <TextField multiline
                  className={classes.formInput} size="small" variant="outlined"
                  id="collection-desc" label="Collection description"
                  value={collectionDesc}
                  onChange={e => setCollectionDesc(e.target.value)}
                />
              </> : <>
                <TextField multiline
                  className={classes.formInput} size="small" variant="outlined" 
                  id={"link-title"} label="Link title"
                  value={linkTitle}
                  onChange={e => setLinkTitle(e.target.value)}
                />
                <TextField required multiline type="url" 
                  className={classes.formInput} variant="outlined" size="small"
                  id="link-url" label="Link URL"
                  value={linkUrl}
                  onChange={e => {
                    setLinkUrl(e.target.value)
                    dispatch({
                      type: ACTIONS.SHOW_SNACKBAR,
                      payload: { message: "Getting link details..." }
                    })
                    handleGetDetails(e.target.value !== undefined ? e.target.value : "")
                  }}
                />
                <TextField select 
                  className={classes.formInput} size="small" variant="outlined"
                  id="link-in-folder-id" label="Collection"
                  value={linkInFolder}
                  onChange={e => setLinkInFolder(e.target.value)}
                >
                  {folders.filter(option => option.folder_name !== "Archive").map(option => (
                    <MenuItem 
                      key={option.folder_id} 
                      value={option.folder_id}
                    >
                      {option.folder_name}
                    </MenuItem>
                  ))}
                </TextField>
              </>
            }
          </DialogContent>
          </div>
          <Fab color="secondary" aria-label="add"
            className={classes.fabAdd} type="submit"
          >
            <SaveIcon />
          </Fab>
        </form>
        <Notifier
          dispatch={formDispatch}
          open={state.openSnackbar}
          message={state.snackbarMessage}
          undoButton={state.snackbarUndoButton}
        />
        <Fab color="secondary" aria-label="cancel" 
          size="small" className={classes.fabCancel}
          onClick={handleClose}
        >
          <ArrowBackIcon />
        </Fab>
      </Dialog>
    </div>
  )
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
    const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
      return () => unsubscribe()
    }, [])
    
    return folderDetails
}

export default EditFormDialog