import React, { useReducer } from 'react'
import { 
  makeStyles,
  List, 
  ListItem,
  ListItemAvatar,
  ListItemText,
  Avatar,
  Grid,
  Dialog,
  Slide,
  Typography,
  Fab,
  CircularProgress
} from "@material-ui/core"

import FolderIcon from '@material-ui/icons/Folder'
import DeleteIcon from '@material-ui/icons/Delete'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'

import StackGrid from "react-stack-grid"
import firebase from "../firebase"
import Folder, { ACTIONS } from '../pages/Folder'
import Header from '../components/Header'
import FormDialog from '../components/FormDialog'
import EditIcon from '@material-ui/icons/Edit'

import { SwipeableListItem, ActionAnimations } from '@sandstreamdev/react-swipeable-list'
import '@sandstreamdev/react-swipeable-list/dist/styles.css'
import EditFormDialog from '../components/EditFormDialog'
import Notifier from '../components/Snackbar'
import { useCollection } from "../services/LinkLibraryServices"
import loadingBg from "../img/loading.png"


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(4)}px)`,
    margin: theme.spacing(10, 2, 18, 2),
    zIndex: 0
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
  avatar: {
    backgroundColor: theme.palette.secondary.main
  },
  folderIcon: {
    color: theme.palette.primary.main
  },
  swipeRightContainer: {
    display: "flex",
    alignItems: "center",
    height: "100%",
    width: "100%",
    background: "#ef5350",
    justifyContent: "flex-start",
  },
  swipeRightAction: {
    display:"flex",
    alignItems: "center",
    color: "#fff",
    alignContent: "flex-start"
  },
  swipeIcon: {
    margin: theme.spacing(0, 1)
  },
  swipeLeftContainer: {
    display: "flex",
    alignItems: "center",
    height: "100%",
    width: "100%",
    background: "#1976d2",
    justifyContent: "flex-end",
  },
  swipeLeftAction: {
    display:"flex",
    alignItems: "center",
    color: "#fff",
    alignContent: "flex-end",
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
  },
  listItem: {
    background: theme.palette.background.paper
  },
  collectionHolder: {
    borderRadius: theme.spacing(1),
    marginBottom: theme.spacing(1),
    background: theme.palette.background.paper
  },
  fabCancel: {
    position: "fixed",
    bottom: theme.spacing(2),
    right: theme.spacing(2),
    zIndex: 1000
  },
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
})

export const COLLECTION_ACTIONS = {
  EDIT_FOLDER: "edit-folder",
  OPEN_FOLDER: "open-folder",
  DELETE_FOLDER: "delete-folder",
  ADD_FOLDER: "add-folder",
  CLOSE_FOLDER: "close-folder",
  SAVE_UPDATED_FOLDER: "save-updated-folder"
}

const collectionDispatch = (state, action) => {
  switch (action.type) {
    case COLLECTION_ACTIONS.EDIT_FOLDER: {
      return {
        ...state,
        showForm: true,
        folderData: action.payload.data
      }
    }
    case COLLECTION_ACTIONS.OPEN_FOLDER: {
      return {
        ...state,
        folderData: action.payload.data,
        openFolder: true
      }
    }
    case COLLECTION_ACTIONS.SAVE_UPDATED_FOLDER: {
      return {
        ...state,
        openSnackbar: true,
        snackbarMessage: action.payload.message,
        snackbarUndoButton: false
      }
    }
    case COLLECTION_ACTIONS.DELETE_FOLDER: {
      firebase.firestore()
        .collection("collections")
        .doc(action.payload.data.folder_id)
        .delete()
        .then(() =>
          console.log("Collection successfully deleted")
        ).catch((err) =>
          console.error("Error removing document: ", err)
        )
      return {
        ...state,
        folderData: action.payload.data,
        openSnackbar: true,
        snackbarMessage: "Collection successfully deleted",
        snackbarUndoButton: true,
      }
    }
    case COLLECTION_ACTIONS.ADD_FOLDER: {
      return {
        ...state,
        openSnackbar: true,
        snackbarUndoButton: false,
        snackbarMessage: action.payload.message
      }
    }
    case COLLECTION_ACTIONS.CLOSE_FOLDER: {
      return {
        ...state,
        openFolder: false
      }
    }
    case ACTIONS.UNDO_DELETE: {
      firebase.firestore()
        .collection("collections")
        .doc(state.folderData.folder_id)
        .set({
          folder_name: state.folderData.folder_name,
          folder_desc: state.folderData.folder_desc
        }).then(() => {
          console.log("Collection restored")
        }).catch(err => {
          console.error("Error restoring collection: ", err)
        })

      return {
        ...state,
        openSnackbar: true,
        snackbarUndoButton: false,
        snackbarMessage: "Collection restored"
      }
    }
    case ACTIONS.CLOSE_SNACKBAR: {
      return {
        ...state,
        openFolder: false,
        openSnackbar: false
      }
    }
    case ACTIONS.CLOSE_EDIT_FORM: {
      return {
        ...state,
        showForm: false
      }
    }
    case ACTIONS.PAGE_LOADING: {
      return {
        ...state,
        isPageLoading: false
      }
    }
    default: return state
  }
}

const initialState = {
  openFolder: false,
  openSnackbar: false,
  snackbarUndoButton: false,
  snackbarMessage: "",
  showForm: false,
  folderData: {},
  isPageLoading: true
}

const Collection = props => {

  console.log("COLLECTION RENDERED")

  const [state, dispatch] = useReducer(collectionDispatch, initialState)
  const classes = useStyles()
  const folderData = useCollection(dispatch)

  const dC = folderData.filter(folder => folder.is_editable === false)
  const uC = folderData.filter(folder => folder.is_editable === undefined)

  const listView = folder => {
    return (
      <Grid item key={folder.folder_id}>
      <SwipeableListItem
        swipeLeft={{
          content:
            <div className={classes.swipeLeftContainer}>
              <div className={classes.swipeLeftAction}>
                <Typography variant="subtitle2">Edit</Typography>
                <EditIcon fontSize="large" className={classes.swipeIcon}/>                
              </div>
            </div>,
          action: () => dispatch({ 
            type: COLLECTION_ACTIONS.EDIT_FOLDER,
            payload: { data: folder }
          }),
          actionAnimation: ActionAnimations.RETURN
        }}
        swipeRight={{
          content: 
            <div className={classes.swipeRightContainer}>
              <div className={classes.swipeRightAction}>
                <DeleteIcon fontSize="large" className={classes.swipeIcon}/>
                <Typography variant="subtitle2">Delete</Typography>
                
              </div>
            </div>,
          action: () => dispatch({
            type: COLLECTION_ACTIONS.DELETE_FOLDER,
            payload: { data: folder }
          }),
          
          actionAnimation: ActionAnimations.REMOVE
        }}
        blockSwipe={folder.is_editable !== undefined ? true : false}
      >
        <ListItem className={classes.listItem} 
          onClick={() => dispatch({
            type: COLLECTION_ACTIONS.OPEN_FOLDER,
            payload: { data: folder }
          })}
        >
          <ListItemAvatar>
            <Avatar className={classes.avatar}>
              <FolderIcon className={classes.folderIcon} />
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary={<Typography variant="subtitle1">{folder.folder_name}</Typography>}
            secondary={folder.folder_desc}
          />
        </ListItem>
      </SwipeableListItem>
    </Grid>
    )
  }
  const defaultCollections = dC.map(folder => listView(folder))
  const userCollections = uC.map(folder => listView(folder))

  return (
    <>
      {/* <Header title={"Collection"} appDispatch={props.dispatch} /> */}
      <div className={classes.root}>
        {state.isPageLoading ? (
          <Grid container spacing={0} direction="column"
            alignItems="center" justify="center" style={{ height: "100%" }}
          >
            <Grid item xs={10} align="center" style={{paddingTop: 16}}>
              <CircularProgress
                color={JSON.parse(localStorage.getItem("theme")).value === false ? "primary" : "secondary"} 
                size={80} thickness={4.5} />
              <Typography variant="subtitle1" gutterBottom>
                Loading your collection
              </Typography>
              <Typography variant="subtitle2" gutterBottom>
                Just sit back and relax
              </Typography>
              <img src={loadingBg} alt="" style={{width: "100%", paddingTop: 8}}></img>
            </Grid>   
          </Grid>
        ) : (<>
          <StackGrid columnWidth={"100%"} gutterWidth={8} gutterHeight={8} >
            <Grid item>
              <List className={classes.collectionHolder}>
                {defaultCollections}
              </List>
            </Grid>
            <Grid item>
              <List className={classes.collectionHolder}>
                {userCollections}
              </List>
            </Grid>
          </StackGrid>
        </>)}
        <Dialog fullScreen open={state.openFolder} TransitionComponent={Transition}>
          <Folder 
            id={state.folderData.folder_id}
            name={state.folderData.folder_name}
          />
          <Fab color="secondary" aria-label="cancel" 
            size="small" className={classes.fabCancel}
            onClick={() => dispatch({
              type: COLLECTION_ACTIONS.CLOSE_FOLDER
            })}
          >
            <ArrowBackIcon />
          </Fab>
        </Dialog>
      </div>
      <Notifier
        dispatch={dispatch}
        open={state.openSnackbar}
        message={state.snackbarMessage}
        undoButton={state.snackbarUndoButton}
      />
      <FormDialog mode={"collection"} dispatch={dispatch} />
      <EditFormDialog mode={"collection"} dispatch={dispatch} 
        edit={state.showForm}
        collection_id={state.folderData.folder_id} 
        folder_name={state.folderData.folder_name} 
        folder_desc={state.folderData.folder_desc}
      />
    </>
  )
}

export default Collection