import React from "react"
import { Dialog, Slide, makeStyles, Fab, Paper, Grid, Typography } from "@material-ui/core"
import ArrowBackIcon from "@material-ui/icons/ArrowBack"
import LinkIcon from "@material-ui/icons/Link"
import { APP_ACTIONS } from "../App"
import aboutBg from "../img/app.png"

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
})

const useStyles = makeStyles(theme => ({
  root: {
    width: `calc(100vw - ${theme.spacing(4)}px)`,
    borderRadius: theme.spacing(1),
    marginBottom: theme.spacing(2)
  },
  header: {
    paddingTop: theme.spacing(7),
    paddingBottom: theme.spacing(4),
    background: "#05307c"
  },
  fabCancel: {
    position: "fixed",
    bottom: theme.spacing(2),
    right: theme.spacing(2),
    zIndex: 1000
  },
}))

export const Settings = ({open, dispatch}) => {

  const classes = useStyles()

  return (
    <Dialog fullScreen open={open}
      TransitionComponent={Transition} 
      aria-labelledby="settings-dialog"
    >
      <div className={classes.header}>
        <img src={aboutBg} width="100%" alt=""></img>
      </div>
      <Grid container spacing={8} style={{paddingBottom:56}}
        alignItems="center" justify="center"
      >
        <Paper variant="outlined" className={classes.root}>
          <Grid item xs={12} md={6} align="center" style={{padding: 16}}>
              <Typography variant="h4" gutterBottom>
                Link library
              </Typography>
              <LinkIcon fontSize="large" style={{marginRight:8}} />
              <Typography variant="subtitle1" gutterBottom>
                Save links and discover the web
              </Typography>
          </Grid>
        </Paper>
        <Paper variant="outlined" className={classes.root}>
          <Grid item xs={12} md={6} align="center" style={{padding: 16}} >
              <Typography variant="subtitle2" gutterBottom>
                Made using <strong>React</strong> and <strong>Material UI</strong><br/>
                Discover links powered by <strong>Currents API</strong>
              </Typography>
              <Typography variant="subtitle1" gutterBottom>
                Image credits
              </Typography>
              <Typography variant="subtitle2" gutterBottom>
                freepik.com / pdabrowski.com
              </Typography>
          </Grid>   
        </Paper>
      </Grid> 
      
      <Fab color="secondary" aria-label="cancel" 
        size="small" className={classes.fabCancel}
        onClick={() => dispatch({ type: APP_ACTIONS.CLOSE_MENU })}
      >
        <ArrowBackIcon />
      </Fab>
    </Dialog>
  )
}