import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import { Card,
	CardActionArea,
	CardActions,
	CardContent,
	CardMedia,
	Typography
} from "@material-ui/core"
import DeleteIcon from "@material-ui/icons/Delete"
import EditIcon from "@material-ui/icons/Edit"
import LinkIcon from '@material-ui/icons/Link'
import LaunchIcon from '@material-ui/icons/Launch'
import IconButton from "@material-ui/core/IconButton"
import AddIcon from '@material-ui/icons/Add'
import ArchiveIcon from '@material-ui/icons/Archive'
import UnarchiveIcon from '@material-ui/icons/Unarchive'

import { CopyToClipboard } from 'react-copy-to-clipboard'
import { ACTIONS, CARD_TYPE } from "../pages/Folder.js"
import { DISCOVER_ACTIONS } from "./DiscoverProvider"

const useStyles = makeStyles(theme => ({
	cardContainer: {
		background: theme.palette.background.paper,
		width: "100%",
		height: "100%",
		borderColor: theme.palette.primary.light,
		borderRadius: theme.spacing(1),
	},
	cardContent: {
		lineHeight: 1.25,
		textOverflow: "ellipsis"
	}
}))


const LinkCard = ({ dispatch, data, type,...props }) => {

	const classes = useStyles()
	let hostName = getHostName(data.link_url)
	const linkDetails = {
		link_title: data.link_title,
		link_url: data.link_url,
		folder_id: data.folder_id,
		link_img: data.link_img,
		date_added: data.date_added,
		is_archived: data.is_archived
	}

	const getCardMenuItems = () => {
		return (
			<CardActions disableSpacing>
				{type === CARD_TYPE.REGULAR
					? ( <>
						{data.is_archived === true
							? (
								<IconButton size="small" color="primary" 
									onClick={() => dispatch({
										type: ACTIONS.UNARCHIVE_LINK,
										payload: { id: data.link_id, data: { is_archived: false } }
									})}>
									<UnarchiveIcon />
								</IconButton>
							) : (
								<IconButton size="small" color="primary" 
									onClick={() => dispatch({
										type: ACTIONS.ARCHIVE_LINK,
										payload: { id: data.link_id, data: { is_archived: true } }
									})}>
									<ArchiveIcon />
								</IconButton>
							)
						}
						<IconButton size="small" color="primary"
							onClick={() => dispatch({ 
								type: ACTIONS.EDIT_LINK,
								payload: { data: { docId: data.link_id, ...linkDetails } }
							})}
						>
							<EditIcon />
						</IconButton>
						<IconButton size="small" color="primary"
							onClick={() => dispatch({
								type: ACTIONS.DELETE_LINK,
								payload: { id: data.link_id, data: linkDetails }
							})}
						>
							<DeleteIcon />
						</IconButton>
					</> ) : ( 
						<IconButton size="small" color="primary"
							onClick={() => dispatch({ 
								type: DISCOVER_ACTIONS.ADD_TO_LIBRARY,
								payload: { data: { ...data, folder_id: "3ppmoaOw6ANJk10oWmhh" } }
							})}
						>
							<AddIcon />
						</IconButton>
					)
				}
				
				<CopyToClipboard text={data.link_url} 
					onCopy={() => dispatch({ 
						type: ACTIONS.COPY_LINK, 
						payload: { message: "Link URL copied to clipboard" } 
					})}
				>
					<IconButton size="small" color="primary">
						<LinkIcon />
					</IconButton>
				</CopyToClipboard>
				<IconButton size="small" color="primary"
					onClick={e => {
						e.preventDefault()
						window.open(data.link_url, "_blank")
					}}
				>
					<LaunchIcon />
				</IconButton>
			</CardActions>
		)
	}

	return (
		<Card variant="outlined" className={classes.cardContainer} >
			<CardActionArea>
				{data.link_img !== "None" 
					? (
						<CardMedia
							component="img"
							alt={data.link_img}
							image={data.link_img}
							title={data.link_title}
							height={type === CARD_TYPE.REGULAR ? "auto" : 120}
						/> 
					) : ( "" )
				}
				<CardContent>
					<Typography variant="overline">
						{hostName}
					</Typography>
					<Typography className={classes.cardContent} variant="subtitle1" gutterBottom>
						{data.link_title}
					</Typography>
					<Typography noWrap className={classes.cardContent} variant="caption" gutterBottom>
						{data.link_url}
					</Typography>
				</CardContent>
			</CardActionArea>
			{getCardMenuItems()}
		</Card>
	)
}

const getHostName = url => {
	const matches = url.match(/^https?:\/\/(www.)?([^/?#]+)(?:[/?#]|$)/i)
	return matches && matches[2]
}

export default LinkCard