import React, { useState, useReducer } from "react"
import CssBaseline from "@material-ui/core/CssBaseline"
import BottomNav from "./components/BottomNav"
import Home from "./pages/Home"
import Collection from "./collection"
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import { ThemeProvider } from '@material-ui/styles'
import { lightTheme, darkTheme } from "./services/Themes.js"
import Discover from "./discover/Discover"
import { Settings } from "./components/Settings"
import Header from "./components/Header"

export const APP_ACTIONS = {
	TOGGLE_THEME: "toggle-theme",
	OPEN_MENU: "open-menu",
	CLOSE_MENU: "close-menu"
}

const appDispatch = (state, action) => {
	switch (action.type) {
		case APP_ACTIONS.TOGGLE_THEME: {
			localStorage.setItem("theme", JSON.stringify(action.payload))
			return {
				...state,
				darkTheme: action.payload.value
			}
		}
		case APP_ACTIONS.OPEN_MENU: {
			return {
				...state,
				showMenu: true
			}
		}
		case APP_ACTIONS.CLOSE_MENU: {
			return {
				...state,
				showMenu: false
			}
		}
		default: return state
	}
}

function App() {

	console.log("APP RENDERED")
	if(!(localStorage.getItem("theme")))
		localStorage.setItem("theme", JSON.stringify({value: false}))

	const initialState = {
		darkTheme: JSON.parse(localStorage.getItem("theme")).value,
		showMenu: false
	}

	const [tab, setTab] = useState("")
	const [state, dispatch] = useReducer(appDispatch, initialState)

	return (
		<ThemeProvider theme={state.darkTheme ? darkTheme : lightTheme} >
			<CssBaseline />
			<Header title={"Link library"} appDispatch={dispatch} show={state.showMenu} />
			<Router basename="/link-library">
				<Switch>
					<Route exact path="/">
						<Home dispatch={dispatch} />
					</Route>
					<Route path="/discover">
						<Discover dispatch={dispatch} />
					</Route>
					<Route path="/collection">
						<Collection dispatch={dispatch} />
					</Route>
				</Switch>
				<BottomNav
					value={tab}
					onChange={setTab}
				/>
			</Router>
			<Settings open={state.showMenu} dispatch={dispatch} />
		</ThemeProvider>
	)
}

export default App