import React from 'react'
import TextField from '@material-ui/core/TextField'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import { makeStyles, Snackbar } from '@material-ui/core'
import Fab from '@material-ui/core/Fab'
import AddIcon from '@material-ui/icons/Add'
import MenuItem from '@material-ui/core/MenuItem'
import SaveIcon from '@material-ui/icons/Save'
import ArrowBackIcon from '@material-ui/icons/ArrowBack'
import { useState, useEffect } from "react"
import { Slide } from "@material-ui/core"

import firebase from "../firebase"
import axios from "axios"
import cheerio from "cheerio"

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import { ACTIONS } from '../pages/Folder'
import { COLLECTION_ACTIONS } from '../collection'

const useStyles = makeStyles(theme => ({
  fabAdd: {
		position: "fixed",
		bottom: theme.spacing(9),
		right: theme.spacing(2),
		zIndex: 1000
  },
  fabCancel: {
    position: "fixed",
    bottom: theme.spacing(2),
    right: theme.spacing(2),
    zIndex: 1000
  },
  formInput: {
    width: "100%",
    margin: theme.spacing(1,0)
  },
  root: {
    padding: theme.spacing(8, 0, 18, 0),
    background: theme.palette.background.paper
  },
  icon: {
    marginRight: theme.spacing(1),
  },
  bottomAppBar: {
    top: "auto",
    bottom: 0
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />
})

const FormDialog = ({ dispatch, ...props }) => {

  const classes = useStyles();
  const folders = useDetails()

  const [open, setOpen] = useState(false)
  const [snackbarOpen, setSnackbarOpen] = useState(false)
  const [snackbarMessage, setSnackbarMessage] = useState("")
  const [collectionName, setCollectionName] = useState("")
  const [collectionDesc, setCollectionDesc] = useState("")
  const [linkInFolder, setLinkInFolder] = useState(props.defaultFolder)
  const [linkTitle, setLinkTitle] = useState("")
  const [linkUrl, setLinkUrl] = useState("")
  const [linkImg, setLinkImg] = useState("")

  const handleClickOpen = () => setOpen(true)

  const handleClose = () => {
    resetFields()
    setSnackbarOpen(false)
    setOpen(false)
  }

  const handleSnackbarClose = () => {
    setSnackbarOpen(false)
  }

  const handleUrlInput = e => {
    setLinkUrl(e.target.value)
    setSnackbarOpen(true)
    setSnackbarMessage("Getting link details...")
    handleGetDetails(e.target.value !== undefined ? e.target.value : "")
  }

  const handleFormSubmit = e => {
    e.preventDefault()
    switch(props.mode) {
      case "collection": {
        firebase.firestore()
        .collection("collections")
        .add({
          folder_name: collectionName,
          folder_desc: collectionDesc
        }).then(() => {
          dispatch({
            type: COLLECTION_ACTIONS.ADD_FOLDER,
            payload: { message: "Collection successfully created" }
          })
          console.log("Collection successfully added")
        }).catch(err => {
          dispatch({
            type: COLLECTION_ACTIONS.ADD_FOLDER,
            payload: { message: "Error creating collection" }
          })
          console.error("Error adding collection: ", err)
        })
        break
      }
      default: 
        firebase.firestore()
        .collection("links")
        .add({
          link_title: linkTitle,
          link_url: linkUrl,
          folder_id: linkInFolder,
          link_img: linkImg,
          date_added: new Date(),
          is_archived: false
        }).then(() => {
          dispatch({ 
            type: ACTIONS.ADD_LINK,
            payload: { message: "Link successfully added" }
          })
          console.log("Link successfully added")
        }).catch(err => {
          dispatch({ 
            type: ACTIONS.ADD_LINK,
            payload: { message: "Error adding link" }
          })
          console.error("Error adding to database: ", err)
        })
    }
    resetFields()
    setOpen(false)
    
  }

  function resetFields() {
    setLinkInFolder(props.defaultFolder)
    setLinkTitle("")
    setLinkUrl("")
    setCollectionName("")
    setCollectionDesc("")
  }

  const handleGetDetails = url => {
    axios.get("https://cors-anywhere.herokuapp.com/" + url)
      .then(
        res => {
          console.log(res.data)
          const $ = cheerio.load(res.data)
          let title = $("meta[property='og:title']").attr("content")
          if(title === undefined) title = $('title')[0].children[0].data
          if(title === undefined) title = ""
          let img = $("meta[property='og:image']").attr("content")
          if(img === undefined) img = $("meta[property='twitter:image']").attr("content")
          if(img === undefined) {
            $("img").each((i, link) => {
              if(/^https:\/\//.test(link.attribs.src)) {
                img = link.attribs.src
              }
            })
          }
          if(img === undefined) img = ""
          
          setLinkImg(img)
          setLinkTitle(title)
          setSnackbarOpen(false)
      }).catch(err => {
        console.log("THIS IS ERROR: ", err)
        setSnackbarMessage("Snap! Failed to get link details")
      })
  }

  return (
    <div>
      <Fab color="secondary" aria-label="add"
        className={classes.fabAdd}
        onClick={handleClickOpen}
      >
        <AddIcon />
      </Fab>
      <Dialog fullScreen open={open}
        TransitionComponent={Transition} 
        aria-labelledby="form-dialog-title"
      >
        <form onSubmit={handleFormSubmit} >
        <div className={classes.root}>
          <DialogTitle id="form-dialog-title">
            Add new {props.mode === "collection" ? props.mode : "link"}
          </DialogTitle>
          <DialogContent>
            {props.mode === "collection"
              ? <>
                <TextField required multiline autoFocus
                  variant="outlined" size="small" className={classes.formInput}
                  id="collection-name" label="Collection name"
                  value={collectionName}
                  onChange={e => setCollectionName(e.target.value)}
                />
                <TextField multiline
                  variant="outlined" size="small" className={classes.formInput}
                  id="collection-desc" label="Collection description"
                  value={collectionDesc}
                  onChange={e => setCollectionDesc(e.target.value)}
                />
              </> : <>
                <TextField multiline
                  variant="outlined" size="small" className={classes.formInput}
                  id={"link-title"} label="Link title"
                  value={linkTitle}
                  onChange={e => setLinkTitle(e.target.value)}
                />
                <TextField type="url" required multiline autoFocus
                  variant="outlined" size="small" className={classes.formInput}
                  id="link-url" label="Link URL"
                  value={linkUrl}
                  onChange={handleUrlInput}
                />
                <TextField select
                  variant="outlined" size="small" className={classes.formInput}
                  id="link-in-folder-id" label="Collection"
                  value={linkInFolder}
                  onChange={e => setLinkInFolder(e.target.value)}
                >
                  {folders.filter(option => option.folder_name !== "Archive").map(option => (
                    <MenuItem 
                      key={option.folder_id} 
                      value={option.folder_id}
                    >
                      {option.folder_name}
                    </MenuItem>
                  ))}
                </TextField>
                {/* <Container style={{
                  marginTop: 8,
                  width: "100%",
                  height: 200,
                  objectFit: "contain",
                }}>
                  Link image preview: 
                  <img width={"100%"} height={200} src={linkImg}></img>
                </Container> */}
              </>
            }
          </DialogContent>
          </div>
          <Fab color="secondary" aria-label="add"
            className={classes.fabAdd}
            type="submit"
          >
            <SaveIcon />
          </Fab>
        </form>
        <Snackbar
          className={classes.snackBar}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={snackbarOpen}
          autoHideDuration={5000}
          onClose={handleSnackbarClose}
          message={snackbarMessage}
          action={
            <>
              <IconButton size="small" aria-label="close" color="inherit" onClick={handleSnackbarClose}>
                <CloseIcon fontSize="small" />
              </IconButton>
            </>
          }
        />
        <Fab color="secondary" aria-label="cancel" 
          size="small" className={classes.fabCancel}
          onClick={handleClose}
        >
          <ArrowBackIcon />
        </Fab>
      </Dialog>
      
    </div>
  );
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
    const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
      return () => unsubscribe()
    }, [])
    
    return folderDetails
}

export default FormDialog