import React, { useState, useEffect, useReducer } from "react"
import axios from "axios"
import LinkCard from "../components/LinkCards"
import { GridList, GridListTile, makeStyles, Typography, useMediaQuery, useTheme } from "@material-ui/core"
import EditFormDialog from "./EditFormDialog"
import { ACTIONS } from "../pages/Folder"
import Notifier from "./Snackbar"

const useStyles = makeStyles(theme => ({
  gridListHolder: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',

  },
  gridList: {
    flexWrap: "nowrap",
    padding: theme.spacing(0,2,1,2),
  },
  discoverHeader: {
    padding: theme.spacing(0,2)
  },
  containerWrapper: {
    margin: theme.spacing(2,0)
  }
}))

export const DISCOVER_ACTIONS = {
  ADD_TO_LIBRARY: "add-to-library",
  SAVE_TO_LIBRARY: "save-to-library"
}

const discoverDispatch = (state, action) => {
  switch (action.type) {
    case DISCOVER_ACTIONS.ADD_TO_LIBRARY: {
      return {
        ...state,
        showForm: true,
        clickedLinkData: action.payload.data
      }
    }
    case ACTIONS.ADD_LINK: {
      return {
        ...state,
        showForm: false,
        openSnackbar: true,
        snackbarMessage: action.payload.message
      }
    }
    case ACTIONS.CLOSE_EDIT_FORM: {
      return {
        ...state,
        showForm: false
      }
    }
    case ACTIONS.CLOSE_SNACKBAR: {
      return {
        ...state,
        openSnackbar: false
      }
    }
    case ACTIONS.COPY_LINK: {
      return {
        ...state,
        openSnackbar: true,
        snackbarMessage: action.payload.message,
      }
    }
    default: return state
  }
}

const initialState = {
  showForm: false,
  openSnackbar: false,
  snackbarUndoButton: false,
  snackbarMessage: "",
  clickedLinkData: {}
}

const DiscoverProvider = ({ title, query }) => {

  const classes = useStyles()
  /* const newsProviderApi = "https://newsapi.org/v2/" */
  const newsProviderApi = "https://api.currentsapi.services/v1/"
  /* const apiKey = "&apiKey=28bc2e36c58a4e4cbe1cc35c93fd54fa" */
  const apiKey = "apiKey=-V2Sp7WAWXtgTlbqlnRQiPwCaBnSviDzx2nzkIOmxAs1QHEl"

  const [state, dispatch] = useReducer(discoverDispatch, initialState)

  const useNews = () => {
    const [news, setNews] = useState([])
    useEffect(() => {
      const unsubscribe = axios.get(newsProviderApi + query + apiKey)
        .then(res => {
          console.log({res})
          setNews(res.data.news)
        }).catch(err => {
          console.log({err})
        })
      return () => unsubscribe
      // TO-DO: PROPER CLEANUP
    }, [])

    return news
  }

  const articles = useNews()
  const articleCards = articles.map(article => (
    <GridListTile key={article.url}>
      <LinkCard
        data={{
          link_title: article.title,
          link_url: article.url,
          link_img: article.image
        }}
        dispatch={dispatch}
      />
    </GridListTile>
  ))

  const useGridSize = () => {
    const theme = useTheme()
    const lg = useMediaQuery(theme.breakpoints.up("lg"))
    const md = useMediaQuery(theme.breakpoints.up("md"))
    const sm = useMediaQuery(theme.breakpoints.up("sm"))

    if (lg) return 7.5
    else if (md) return 5.5
    else if (sm) return 3.5
    else return 1.5
  }

  return (
    <>
      <div className={classes.containerWrapper} >
        <div className={classes.discoverHeader}>
          <Typography variant="h6" gutterBottom>
            {title}
          </Typography>
        </div>
        <div className={classes.gridListHolder} >
          <GridList className={classes.gridList} cols={useGridSize()} cellHeight={"auto"} spacing={8}>
            {articleCards}
          </GridList>
        </div>
      </div>
      <Notifier 
        dispatch={dispatch}
        open={state.openSnackbar}
        message={state.snackbarMessage}
        undoButton={state.snackbarUndoButton}
      />
      <EditFormDialog mode={"link"} from={"discover"}
        dispatch={dispatch} edit={state.showForm} {...state.clickedLinkData} />
    </>
  )
}

export default DiscoverProvider