import React, { useReducer } from "react"
import firebase from "../firebase"
import { Grid, makeStyles, Typography, Container, CircularProgress, Fade } from "@material-ui/core"
import StackGrid from "react-stack-grid"
import LinkCard from "../components/LinkCards"

import FormDialog from "../components/FormDialog"
import EditFormDialog from "../components/EditFormDialog"
import Notifier from "../components/Snackbar"

import errorBg from "../img/404-illustration.svg"
import loadingBg from "../img/loading.png"

import { useLinks, useWindowSize } from "../services/LinkLibraryServices"

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(2)}px)`,
    margin: theme.spacing(10,1,18,1)
  },
  folderNameHeader: {
    background: theme.palette.background.paper,
    zIndex: 5,
    paddingBottom: theme.spacing(1)
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

export const ACTIONS = {
  ADD_LINK: "add-link",
  COPY_LINK: "copy-link",
  EDIT_LINK: "edit-link",
  DELETE_LINK: "delete-link",
  ARCHIVE_LINK: "archive-link",
  UNARCHIVE_LINK: "unarchive-link",
  UNDO_DELETE: "undo-delete-link",
  SHOW_SNACKBAR: "show-snackbar",
  CLOSE_SNACKBAR: "close-snackbar",
  CLOSE_EDIT_FORM: "close-edit-form",
  PAGE_LOADING: "page-loading"
}

export const CARD_TYPE = {
  REGULAR: "regular-link-card",
  DISCOVER: "discover-link-card"
}

const linkCardsReducer = (state, action) => {
  switch (action.type) {
    case ACTIONS.ADD_LINK: {
      return {
        ...state,
        snackbarUndoButton: false,
        openSnackbar: true,
        snackbarMessage: action.payload.message,
        showForm: false
      }
    }
    case ACTIONS.COPY_LINK: {
      return {
        ...state,
        snackbarUndoButton: false,
        openSnackbar: true,
        snackbarMessage: action.payload.message
      }
    }
    case ACTIONS.EDIT_LINK: {
      return {
        ...state,
        showForm: true,
        clickedLinkData: action.payload.data
      }
    }
    case ACTIONS.DELETE_LINK: {
      firebase.firestore()
        .collection("links")
        .doc(action.payload.id)
        .delete()
        .then(() => {
          console.log("Success")
        }).catch((err) => {
          console.error("Error removing document: ", err)
        })
      
      return {
        snackbarMessage: "Link successfully deleted",
        openSnackbar: true,
        showForm: false,
        clickedLinkData: action.payload.data,
        snackbarUndoButton: true
      }
    }
    case ACTIONS.ARCHIVE_LINK: {
      firebase.firestore()
        .collection("links")
        .doc(action.payload.id)
        .update(action.payload.data)
        .then(() => {
          console.log("Archived")
        }).catch((err) => {
          console.error("Error archiving: ", err)
        })

      return {
        ...state,
        openSnackbar: true,
        snackbarMessage: "Link archived",
        snackbarUndoButton: false
      }
    }
    case ACTIONS.UNARCHIVE_LINK: {
      firebase.firestore()
        .collection("links")
        .doc(action.payload.id)
        .update(action.payload.data)
        .then(() => {
          console.log("Removed from archive")
        }).catch((err) => {
          console.error("Error removing from archive: ", err)
        })

      return {
        ...state,
        openSnackbar: true,
        snackbarMessage: "Link removed from archive",
        snackbarUndoButton: false
      }
    }
    case ACTIONS.UNDO_DELETE: {
      firebase.firestore()
        .collection("links")
        .add(state.clickedLinkData)
        .then(() => console.log("Link delete undone"))
        .catch(err => console.error("Error encountered: ", err))
      
      return {
        ...state,
        openSnackbar: true,
        snackbarUndoButton: false,
        snackbarMessage: "Link restored"
      }
    }
    case ACTIONS.CLOSE_SNACKBAR: {
      return {
        ...state,
        openSnackbar: false
      }
    }
    case ACTIONS.CLOSE_EDIT_FORM: {
      return {
        ...state,
        showForm: false
      }
    }
    case ACTIONS.PAGE_LOADING: {
      return {
        ...state,
        isPageLoading: false
      }
    }
    default: return state
  }
}

const initialState = {
  openSnackbar: false,
  showForm: false,
  snackbarMessage: "",
  snackbarUndoButton: false,
  clickedLinkData: {},
  isPageLoading: true
}

const Folder = ({ id, name, ...props}) => {

  const classes = useStyles()
  const [state, dispatch] = useReducer(linkCardsReducer, initialState)

  const linkData = useLinks(dispatch)

  const filterLinks = () => {
    if (props.fetchAll) {
      return linkData.filter(link => link.is_archived === false).map(link => cardView(link))
    } else {
      if (name === "Archive") {
        return linkData.filter(link => link.is_archived === true).map(link => cardView(link))
      } else return linkData.filter(link => link.folder_id === id).map(link => cardView(link))
    }
  }

  const cardView = link => {
    return (
      <Grid item key={link.link_id}>
        <LinkCard
          data={link}
          type={CARD_TYPE.REGULAR}
          dispatch={dispatch}
        />
      </Grid>
    )
  }

  const links = filterLinks()
  const size = useWindowSize()
    
  return (
    <>
      <div className={classes.root} >
        {props.showHeader === undefined ? (
            <Container className={classes.folderNameHeader}>
              <Typography variant="h6" gutterBottom>{name}</Typography>
            </Container>
          ) : ( "" )
        }
        {state.isPageLoading ? (
          <Grid container spacing={0} direction="column"
            alignItems="center" justify="center" style={{ height: "100%" }}
          >
            <Grid item xs={10} align="center" style={{paddingTop: 16}}>
              <CircularProgress 
                color={JSON.parse(localStorage.getItem("theme")).value === false ? "primary" : "secondary"} 
                size={80} thickness={4.5} />
              <Typography variant="subtitle1" gutterBottom>
                Loading your links
              </Typography>
              <Typography variant="subtitle2" gutterBottom>
                Just sit back and relax
              </Typography>
              <img src={loadingBg} alt="" style={{width: "100%", paddingTop: 8}}></img>
            </Grid>   
          </Grid> 
        ) : (<>
          {links.length ? (
            <StackGrid monitorImagesLoaded={true}
              columnWidth={size.width <= 320 ? size.width-16 : 165}
              gutterWidth={8} gutterHeight={8}
            >
              {links}
            </StackGrid>
          ) : ( <>
            <Fade in={!state.isPageLoading} >
              <Grid container spacing={0} direction="column"
                alignItems="center" justify="center" style={{ height: "100%" }}
              >
                <Grid item xs={10} align="center">
                  <Typography variant="subtitle1" gutterBottom>
                    Nothing to see here
                  </Typography>
                  <Typography variant="subtitle2" gutterBottom>
                    Click the + button to add to this collection
                  </Typography>
                  <img src={errorBg} alt="" style={{width: "100%", paddingTop: 8}}></img>
                </Grid>   
              </Grid>
            </Fade>
          </>)}
        </>)}
        <Notifier 
          dispatch={dispatch}
          open={state.openSnackbar}
          message={state.snackbarMessage}
          undoButton={state.snackbarUndoButton}
        />
      </div>
      <FormDialog mode={"link"} dispatch={dispatch} defaultFolder={id}/>
      <EditFormDialog mode={"link"} from={"folder"} dispatch={dispatch}
        edit={state.showForm} {...state.clickedLinkData} />
    </>
  )
}

export default Folder