import React, { useState } from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import BookmarksIcon from '@material-ui/icons/Bookmarks'
import InvertColorsIcon from '@material-ui/icons/InvertColors'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import InfoIcon from '@material-ui/icons/Info'
import { IconButton, Menu, MenuItem, Slide, useScrollTrigger } from '@material-ui/core'
import { APP_ACTIONS } from "../App"

const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'flex',
    flexDirection: "column",
    alignItems: "flex-start",
    textTransform: "uppercase"
  },
  appBar: {
    position: "fixed",
    zIndex: 8000,
    top: theme.spacing(1),
    width: `calc(100% - ${theme.spacing(2)}px)`,
    left: theme.spacing(1),
    borderRadius: theme.spacing(1)
  }
}));

const HideOnScroll = props => {
  const { children, window } = props;
  const trigger = useScrollTrigger({ target: window ? window() : undefined });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  )
}

const Header = ({ appDispatch, ...props }) => {
  const classes = useStyles()

  const [anchorEl, setAnchorEl] = useState(null)
  const handleClick = event => setAnchorEl(event.currentTarget)
  const handleClose = () => setAnchorEl(null)

  return (
    <HideOnScroll {...props}>
      <AppBar className={classes.appBar}>
        <Toolbar>
          <BookmarksIcon style={{marginRight: 8}}/>
          <div className={classes.title}>
            <Typography variant="h5">
              {props.title}
            </Typography>
          </div>
          <IconButton style={{marginRight:-16}} 
            onClick={handleClick}>
            <MoreVertIcon style={{color: "#fff"}}/>
          </IconButton>
          <Menu id="simple-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleClose}
            style={{zIndex:11000}}
          >
            <MenuItem onClick={() => {
              appDispatch({
                type: APP_ACTIONS.TOGGLE_THEME,
                payload: { value: !(JSON.parse(localStorage.getItem("theme")).value) }
              })
              handleClose()
            }}>
              <InvertColorsIcon style={{marginRight:8}} /> Toggle theme
            </MenuItem>
            {!props.show ? (
              <MenuItem onClick={() => {
                appDispatch({
                  type: APP_ACTIONS.OPEN_MENU
                })
                handleClose()
              }}>
                <InfoIcon style={{marginRight:8}} /> About app
              </MenuItem>
            ) : ( "" )}
          </Menu>
        </Toolbar>
      </AppBar>
    </HideOnScroll>
  )
}

export default Header